//
// Component: Button
//

.btn {
  &.disabled,
  &:disabled {
    cursor: not-allowed;
  }

  // Flat buttons
  &.btn-flat {
    @include border-radius(0);
    border-width: 1px;
    box-shadow: none;
  }

  // input file btn
  &.btn-file {
    overflow: hidden;
    position: relative;

    > input[type="file"] {
      background-color: $white;
      cursor: inherit;
      display: block;
      font-size: 100px;
      min-height: 100%;
      min-width: 100%;
      opacity: 0;
      outline: none;
      position: absolute;
      right: 0;
      text-align: right;
      top: 0;
    }
  }

  .text-sm & {
    font-size: $font-size-sm !important;
  }
}

// Button color variations
.btn-default {
  @include button-variant(
    $button-default-background-color,
    $button-default-border-color,
    $color: $button-default-color,
    $hover-background: shade-color($button-default-background-color, 5%),
    $hover-color: shade-color($button-default-color, 10%),
    $active-background: shade-color($button-default-background-color, 5%),
    $active-color: shade-color($button-default-color, 10%),
    $disabled-background: $button-default-background-color,
    $disabled-color: $button-default-color
  );

  --#{$prefix}btn-focus-shadow-rgb: #{to-rgb(mix(color-contrast($primary), $primary, 15%))}; //***

