// Application buttons
.btn-app {
  @include border-radius(3px);
  background-color: $button-default-background-color;
  border: 1px solid $button-default-border-color;
  color: $gray-600;
  font-size: 12px;
  height: 60px;
  margin: 0 0 10px 10px;
  min-width: 80px;
  padding: 15px 5px;
  position: relative;
  text-align: center;

  // Icons within the btn
  > .fa-solid,
  > .fa-regular,
  > .fa-light,
  > .fa-thin,
  > .fa-duotone,
  > .fa-brands,
  > .fa,
  > .fas,
  > .far,
  > .fab,
  > .fal,
  > .fad,
  > .svg-inline--fa,
  > .ion {
    display: block;
    font-size: 20px;
  }

  > .svg-inline--fa {
    margin: 0 auto;
  }

  &:hover {
    background-color: $button-default-background-color;
    border-color: shade-color($button-default-border-color, 20%);
    color: $button-default-color;
  }

  &:active,
  &:focus {
    @include box-shadow(inset 0 3px 5px rgba($black, .125));
  }

  // The badge
  > .badge {
    font-size: 10px;
    font-weight: 400;
    position: absolute;
    right: -10px;
    top: -3px;
  }
}

// Extra Button Size

.btn-xs {
  @include button-size($button-padding-y-xs, $button-padding-x-xs, $button-font-size-xs, $button-border-radius-xs); //***
}

@include dark-mode () {
  .btn-default,
  .btn-app {
    @include button-variant(
      tint-color($dark, 2.5%),
      $gray-600,
      $color: $white,
      $hover-background: tint-color($dark, 5%),
      $hover-border: tint-color($gray-600, 2.5%),
      $hover-color: $gray-300,
      $active-background: tint-color($dark, 5%),
      $active-border: tint-color($gray-600, 2.5%),
      $active-color: $gray-300
    );
    // background-color: tint-color($dark, 2.5%);
    // color: $white;
    // border-color: $gray-600;

    // &:hover,
    // &:focus {
    //   background-color: tint-color($dark, 5%);
    //   color: $gray-300;
    //   border-color: tint-color($gray-600, 2.5%);
    // }
  }
  .btn-light {
    @include button-variant(
      tint-color($dark, 7.5%),
      $gray-600,
      $color: $white,
      $hover-background: tint-color($dark, 10%),
      $hover-border: tint-color($gray-600, 5%),
      $hover-color: $gray-300,
      $active-background: tint-color($dark, 10%),
      $active-border: tint-color($gray-600, 5%),
      $active-color: $gray-300
    );
    // background-color: tint-color($dark, 7.5%);
    // color: $white;
    // border-color: $gray-600;

    // &:hover,
    // &:focus {
    //   background-color: tint-color($dark, 10%);
    //   color: $gray-300;
    //   border-color: tint-color($gray-600, 5%);
    // }
  }

  @each $color, $value in $theme-colors-alt {
    .btn-#{$color} {
      @if $color == dark {
        @include button-variant(shade-color($value, 5%), tint-color($value, 10%));
      } @else {
        @include button-variant($value, $value);
      }
    }
  }

  @each $color, $value in $theme-colors-alt {
    .btn-outline-#{$color} {
      @if $color == dark {
        @include button-outline-variant(shade-color($value, 20%));
      } @else {
        @include button-outline-variant($value);
      }
    }
  }
}
