//
// Mixins: Accent
//

// Accent Variant
@mixin accent-variant($name, $color) {
  $link-color: $color;
  .accent-#{$name} {
    $link-hover-color: shade-color($color, 15%);
    $pagination-active-bg: $color;
    $pagination-active-border-color: $color;

    .btn-link,
    a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link):not(.page-link):not(.badge):not(.btn),
    .nav-tabs .nav-link {
      color: $link-color;

      &:hover {
        color: $link-hover-color;
      }
    }

    .dropdown-item {
      &:active,
      &.active {
        background-color: $color;
        color: color-contrast($color);
      }
    }

    .form-control:focus:not(.is-invalid):not(.is-warning):not(.is-valid),
    .form-select:focus {
      border-color: tint-color($color, 25%);
    }

    .page-item {
      .page-link {
        color: $link-color;
      }

      &.active a,
      &.active .page-link {
        background-color: $pagination-active-bg;
        border-color: $pagination-active-border-color;
        color: $pagination-active-color;
      }

      &.disabled a,
      &.disabled .page-link {
        background-color: $pagination-disabled-bg;
        border-color: $pagination-disabled-border-color;
        color: $pagination-disabled-color;
      }
    }

    [class*="sidebar-dark-"] {
      .sidebar {
        a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link) {
          color: $sidebar-dark-color;

          &:hover {
            color: $sidebar-dark-hover-color;
          }
        }
      }
    }

    [class*="sidebar-light-"] {
      .sidebar {
        a:not(.dropdown-item):not(.btn-app):not(.nav-link):not(.brand-link) {
          color: $sidebar-light-color;

          &:hover {
            color: $sidebar-light-hover-color;
          }
        }
      }
    }
  }
  @include dark-mode () {
    &.accent-#{$name} {
      .page-item {
        .page-link {
          &:hover,
          &:focus {
            color: tint-color($link-color, 5%);
          }
        }
      }
    }
  }
}
